@import "../vars";
// @import "../fonts"
// @import "../libs"

.first-screen {
    position: relative;
    &:after {
        // content: ' '
        // background-color: #fff
        background-image: url('/img/divider.svg');
        // background-size:
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 150px; } }

.cta-screen {
    &_home {
        background-image: url('/img/landing-first.jpg');
        background-position: center center; }
    &_last {
        background-image: url('/img/landing-last.jpg');
        background-position: center right; } }





.second-screen {

  h2 {
    margin-top: 8rem;
    margin-bottom: 8rem; }
  .about {
    margin-bottom: 8rem;
    justify-content: center;
    display: flex;
    align-items: center;
    h3 {
      margin-bottom: 1.5rem; }
    &:nth-child(even) {
      flex-direction: row-reverse;
      .about__text {
        margin-left: 8.3333%; } }
    &:nth-child(odd) {
      .about__img {
        margin-left: 8.3333%; } } } }


.third-screen {
  background-image: url('/img/how-it-works.png');
  background-size: cover;
  .process {
    &-wrapper {
      background-color: $white;
      border-radius: .5rem;
      margin-top: 8rem;
      margin-bottom: 8rem;
      padding: 8rem 4rem; }
    &-container {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      text-align: center; }
    &__step {
      width: 30%;
      margin-top: 4rem;
      position: relative;
      &__number {
        position: absolute;
        top: 0;
        left: 2rem;
        font-weight: 200;
        font-size: 4rem;
        color: $light-gray; } }
    &__icon {
      width: 8rem;
      margin-bottom: 2rem; }
    &__header {
      font-size: 2rem;
      margin-bottom: 1rem;
      font-weight: 500; } } }

.fourth-screen {
  h2 {
    margin-top: 8rem; } }

.feedbacks {
  margin-top: 8rem; }

.feedback {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 4rem;
  .author {
    display: flex;
    flex-direction: row;
    align-items: center;
    &__img {
      width: 8rem;
      height: 8rem;
      border-radius: 50%; }
    &__label {
      margin-left: 2rem; }
    &__name {
      font-size: 2em;
      font-weight: 600; }
    &__problem {
      font-size: 0.75rem;
      font-weight: 600;
      color: $light-gray;
      text-transform: uppercase; } }
  &__text {
    margin-top: 2rem;
    text-align: center;
    font-weight: 300;
    padding: 0 2rem; } }

